import { Link } from 'gatsby';
import React, { CSSProperties, FC, useState } from 'react';
import * as style from './index.module.scss';

const mapColor: Map<string, string> = new Map([
    ['job', '#fd5f5f'],
    ['side project', '#fd995f'],
    ['web', '#fdf25f'],
    // ['side project', '#bffda2'],
    // ['web', '#5fbefd'],
    ['game', '#775ffd'],
    ['IoT', '#b65ffd'],
    // ['AI, "#ff8af5"],
])
const strColorIdle = '#8b8b8b';
const strColorHover = '#67676b';
const strColorClicked = '#5b5b5e';

type Props = {
    name: string,
    isFocus?: boolean,
    navigateTo?: string,
}
const Tab: FC<Props> = ({ name, isFocus=false, navigateTo }) => {
    const [isHover, setHoverState] = useState(false);

    const css: CSSProperties = {
        borderLeft: `solid 5px ${mapColor.get(name) ?? 'white'}`,
        backgroundColor: (isFocus ? strColorClicked : (isHover ? strColorHover : strColorIdle)),
    }
    return (
        <Link
            to={navigateTo ?? `/projects/?tabs=${encodeURIComponent(name)}`}
        >
            <div
                style={css}
                className={style.div}
                onMouseEnter={() => setHoverState(true)}
                onMouseLeave={() => setHoverState(false)}
            >
                <span>{name}</span>
            </div>
        </Link>
    )
}
export default Tab;
